      translate: 0px 4px;
    }
  
    60% {
      z-index: 8;
      translate: 0px 8px;
    }
  
    80% {
      z-index: 7;
      opacity: 1;
      translate: 0px 12px;
    }
  
    100% {
      z-index: 5;
      translate: 0px 30px;
      opacity: 0;
    }
  }`, "",{"version":3,"sources":["webpack://./src/Components/StackLoader/style.css"],"names":[],"mappings":"AAAA,aAAa;AACb,0DAA0D;;AAE1D;IACI,QAAQ;IACR,YAAY;IACZ,WAAW;EACb;;EAEA;IACE,kBAAkB;IAClB,UAAU;IACV,UAAU;EACZ;;EAEA;IACE,kBAAkB;IAClB,yBAAyB;IACzB,WAAW;IACX,WAAW;IACX,6BAA6B;IAC7B,SAAS;IACT,UAAU;EACZ;;EAEA;IACE,kBAAkB;IAClB,yBAAyB;IACzB,WAAW;IACX,WAAW;IACX,4BAA4B;IAC5B,SAAS;IACT,UAAU;EACZ;;EAEA;IACE,kBAAkB;IAClB,yBAAyB;IACzB,WAAW;IACX,YAAY;IACZ,aAAa;IACb,+BAA+B;EACjC;;EAEA;IACE,gCAAgC;EAClC;;EAEA;IACE,iCAAiC;IACjC,mBAAmB;EACrB;;EAEA;IACE,gCAAgC;IAChC,mBAAmB;EACrB;;EAEA;IACE,iCAAiC;IACjC,mBAAmB;EACrB;;EAEA;IACE;MACE,WAAW;MACX,UAAU;MACV,qBAAqB;IACvB;;IAEA;MACE,WAAW;MACX,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,UAAU;MACV,mBAAmB;IACrB;;IAEA;MACE,UAAU;MACV,mBAAmB;MACnB,UAAU;IACZ;EACF;;EAEA;IACE;MACE,WAAW;MACX,UAAU;MACV,oBAAoB;IACtB;;IAEA;MACE,WAAW;MACX,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,kBAAkB;IACpB;;IAEA;MACE,UAAU;MACV,UAAU;MACV,mBAAmB;IACrB;;IAEA;MACE,UAAU;MACV,mBAAmB;MACnB,UAAU;IACZ;EACF","sourcesContent":["/* Credits: */\n/* 3D tower loader made by: csozi | Website: www.csozi.hu*/\n\n.loader {\n    scale: 3;\n    height: 50px;\n    width: 40px;\n  }\n  \n  .box {\n    position: relative;\n    opacity: 0;\n    left: 10px;\n  }\n  \n  .side-left {\n    position: absolute;\n    background-color: #286cb5;\n    width: 19px;\n    height: 5px;\n    transform: skew(0deg, -25deg);\n    top: 14px;\n    left: 10px;\n  }\n  \n  .side-right {\n    position: absolute;\n    background-color: #2f85e0;\n    width: 19px;\n    height: 5px;\n    transform: skew(0deg, 25deg);\n    top: 14px;\n    left: -9px;\n  }\n  \n  .side-top {\n    position: absolute;\n    background-color: #5fa8f5;\n    width: 20px;\n    height: 20px;\n    rotate: 45deg;\n    transform: skew(-20deg, -20deg);\n  }\n  \n  .box-1 {\n    animation: from-left 4s infinite;\n  }\n  \n  .box-2 {\n    animat